        <p><strong>Usually made:</strong> {{ recipe.when_usually_made }}</p>
        <p><strong>Ingredients:</strong></p>
        <ul>
          <li v-for="ingredient in recipe.ingredients" :key="ingredient">{{ ingredient }}</li>
        </ul>
        <p><strong>Instructions:</strong></p>
        <p>{{ recipe.instructions }}</p>
      </div>
    </div>
  </template>
  
  <script>
  import { mockGetRecipesFamily } from "../services/recipes.js";
  
  export default {
    data() {
      return {
