      };
    },
    created() {
      const response = mockGetRecipesFamily();
      this.recipes = response.data;
    }
  };
  </script>
  
  <style scoped>
  .full-width-image {
    width: 100%;
    height: auto;
  }
  
  .recipe-section {
    margin: 20px 0;
  }
  
  .recipe-image {
    width: 100px;
    height: auto;
  }
  </style>